import MetaInfo from 'vue-meta-info';
import VueCookies from 'vue-cookies'
import AudioVisual from 'vue-audio-visual'
import uploader from 'vue-simple-uploader'
import App from './App.vue';

//
import router from "@/router";
//
import store from "@/store";

import { Button,MessageBox} from 'element-ui';
//
// import "@/plugins/validate";
	
//
Vue.component(Button.name,Button);

//ElementUI
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;
//
import * as API from '@/api';
//
Vue.config.productionTip = false
Vue.use(ElementUI);
// Vue.use(VueAudio);
Vue.use(MetaInfo);
Vue.use(VueCookies)
Vue.use(AudioVisual)
Vue.use(uploader)
// pxrem
/*  start */
import { monitorZoom } from "@/utils/monitorZoom.js";

const userAgentInfo = navigator.userAgent;
const mobileAgents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'];
let mobileFlag = false;

for (let i = 0; i < mobileAgents.length; i++) {
  if (userAgentInfo.indexOf(mobileAgents[i]) > 0) {
    mobileFlag = true;
    break;
  }
}
if (mobileFlag) {
  document.body.style.zoom = 0.75;
} else {
  const m = monitorZoom();
  console.log(window.screen.width * window.devicePixelRatio);
  if (window.screen.width * window.devicePixelRatio >= 3840) {
    document.body.style.zoom = 100 / (Number(m) / 2); //  4k 
  } else if (window.screen.width * window.devicePixelRatio >= 2560) {
    document.body.style.zoom = 0.75; //  2k 
  } else if (window.screen.width * window.devicePixelRatio >= 1920) {
    document.body.style.zoom = 100 / (Number(m) / 1.5); //  2k 
  } else if (window.screen.width * window.devicePixelRatio >= 1440) {
    document.body.style.zoom = 100 / (Number(m) / 1.1); //  2k 
  } else {
    document.body.style.zoom = 100 / Number(m);

}

}


 
/*  end */
new Vue({
  
  //$bus
  beforeCreate() {
    Vue.prototype.$bus = this;
    Vue.prototype.$API = API;
  },
  //router
  router,
  //store
  store,
  render: h => h(App),
}).$mount('#app')
